	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","description":"I am a developer, designer and product manager from France. Co-founder of Pixelnest Studio.","author":"Matthieu Oger","siteUrl":"https://solarsailer.net/"}},"markdownRemark":{"html":"<blockquote>\n<p>The rule of two is that <strong>two is one — and one is none.</strong> This is applicable to so many things in your life. As a starting point, I often like to think of the rule of two with things that you have around the house. So, for example, if you have one roll of toilet paper, you really don't have any toilet paper. Because when that one runs out, you're in trouble. So you really need two rolls of toilet paper at all time. It's a redundancy rule, basically. It's where this comes from. […]</p>\n<p>This is one of my little pieces of advice for trying to run a life very smoothly. It's that, everything that you can possibly have two of, you should have it. Two shampoo bottles, two bottles of vitamins, two boxes of cereal, two cartons of eggs. You want duplicates of everything. And then, when you're down to one of those things, that's the sign that you need to buy the next one. In this way, you are never out, you're never out of anything. […]</p>\n<p>It's applicable to everything in your whole life, everything that's important.</p>\n<p>— <a href=\"http://www.relay.fm/cortex/12\">CGP Grey</a></p>\n</blockquote>\n<p>(Emphasis mine)</p>\n<p>This is so spot on. I try to function like that, but I never put words onto this behavior. Here it is.</p>\n<p>Grey continues:</p>\n<blockquote>\n<p>Think this way with computer files: you have only one copy of that file, guess what? You have no copy of this file. I even think it's applicable to work. If you have one source of income, in many ways, it's like you have no source of income. Because if something happens with your main job, you are in lots and lots of trouble. One source of income, no source of income.</p>\n</blockquote>","frontmatter":{"date":"2015-09-09","readableDate":"09 September 2015","title":"Quote — CGP Grey, the rule of two","showPageTitle":null,"excerptLine":null,"excerptQuote":{"name":"CGP Grey","quote":"The rule of two is that two is one — and one is none."}},"fields":{"path":"/2015/09/rule-of-two/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2015-09-09-rule-of-two.md absPath of file >>> MarkdownRemark"}}

